@import "../../styles/variables.scss";

.login-form {
	margin-bottom: auto;
	margin-top: auto;
	min-width: 400px;
	min-height: 400px;
	padding: 1rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	border: 1px solid $main-color;
	border-radius: 5px;

	& > input {
		align-self: stretch;
		border-radius: 5px;
		background-color: $bg-color;
		border: none;
		padding: 0.2rem;
		font-size: 1.5rem;
		color: $main-color;

		&:focus {
			outline: 1px solid $text-color;
		}
	}
	& > button {
		line-height: 2rem;
		font-size: 1.5rem;
		padding: 0px 1.5rem;
		border-radius: 5px;
		border: none;
		color: $bg-color;
		background-color: $main-color;
		transition: 0.2s;

		&:hover {
			color: $main-color;
			background-color: $bg-color;
		}
	}
}

.login-form__title {
	color: $text-color;
	font-size: 2rem;
}
