@import "../../styles/variables.scss";

.chat-item {
	padding: 0rem 1rem;
	font-size: 1rem;

	display: flex;
	flex-direction: column;
	align-items: stretch;

	background-color: rgba(0, 0, 0, 0.2);

	border-bottom: 0.2rem solid $text-color;
	border-top: 0.2rem solid $text-color;
	border-left: 1px solid $text-color;
	border-right: 1px solid $text-color;
	border-radius: 15px;
	margin-bottom: 1rem;

	&.chat-item_guest {
		margin-right: auto;
	}
}

.chat-item__top {
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	align-items: flex-end;
	border-bottom: 1px solid $bg-color;
	padding: 0.5rem 0rem;

	.chat-item_guest & {
		flex-direction: row;
	}
}

.chat-item__bottom {
	display: flex;
	flex-direction: row-reverse;
	padding: 0.5rem 0rem;

	.chat-item_guest & {
		flex-direction: row;
	}
}

.chat-item__date {
	filter: brightness(85%);
	font-size: 0.8em;
}

.chat-item__username {
	font-size: 1.2em;
	color: $main-color;
}
