@import "../../styles/variables.scss";

.chat-input {
	border-top: 2px solid $text-color;
	display: flex;
	padding: 2rem 0.5rem;
	background-color: rgba(255, 255, 255, 0.06);

	& > input {
		flex: 1;
		border-radius: 5px;
		background-color: $bg-color;
		border: none;
		padding: 0.2rem;
		font-size: 1rem;
		color: $main-color;

		&:focus {
			outline: 1px solid $text-color;
		}
	}

	& > button {
		line-height: 2rem;
		font-size: 1.5rem;
		padding: 0px 1.5rem;
		margin-left: 0.5rem;
		border-radius: 5px;
		border: none;
		color: $bg-color;
		background-color: $main-color;
		transition: 0.2s;

		&:hover {
			color: $main-color;
			background-color: $bg-color;
		}
	}
}
